.container {
  display: flex;
  height: 100vh;
  background: #f3f3f3;
}

/* RIGHT SIDE */
.main {
  flex: 1;
  display: flex;
  flex-direction: column;
}

/* PAGE CONTENT */
.page {
  padding: 20px;
  background: #ffffff;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  flex: 1;
  overflow-y: auto;
}
